/******************************************************************

Stylesheet: WP Images

******************************************************************/

.size-auto,
.size-full,
.size-large,
.size-medium,
.size-thumbnail {
	max-width: 100%;
	height: auto;
}

@include breakpoint(small) {
	.alignleft, img.alignleft, figure.alignleft, dl.alignleft {
		display: inline;
		float: left;
		margin-left: 0em;
		margin-right: $default_margin;
	}
	.alignright, img.alignright, figure.alignright, dl.alignright {
		display: inline;
		float: right;
		margin-left: $default_margin;
		margin-right: 0em;
	}
	.aligncenter, img.aligncenter, figure.aligncenter, dl.aligncenter {
		clear: both;
		display: block;
		margin-right: auto;
		margin-left: auto;
	}
}

.alignnone, img.alignnone, figure.alignnone, dl.alignnone, .alignnone img  {
	margin-left: 0;
	margin-right: 0;
	// width: 100% !important;
	height: auto;
}

/*********************
WP CAPTIONS
*********************/

.wp-caption {
	max-width: 100%;
	text-align: center;
	&.alignleft {
		margin-right: $default_margin;
	}
	&.alignright {
		margin-left: $default_margin;
	}
	&.aligncenter {
		margin-right: auto;
		margin-left: auto;
	}

	/* images inside wp-caption */
	img {
		max-width: 100%;
		height: auto;
		margin-bottom: 0;
	}

	.wp-caption-text {
		font-size: 0.875em;
		margin: .5em 0 1em;
		text-align: center;
	}
}


/*********************
HTML5 IMAGE GALLERY
*********************/

.gallery {
	text-align: justify;
	.gallery-item {
		margin: 0;
		width: 50%;
		display: inline-block;
		vertical-align: top;
		padding: .5em;
		// outline: none;
	}
	img {
		display: block;
	}
	.gallery-caption {
		text-align: left;
		padding: .25em 0;
	}
	&:after {
	    content: '';
	    display: inline-block;
	    width: 100%;
	}
@include breakpoint(medium) {
	.gallery-item {
		width: 25%;
	}
	img {
		will-change: opacity;
		&:hover {
			opacity: .7;
			transition: opacity $ease;
		}
	}
}
}
